cciones) y un fotodetector ajustado a la curva de sensibilidad fotópica del ojo humano y situado en una pequeña ventana frontal sobre la esfera en donde eventualmente converge todo el flujo luminoso de cualquier fuente lumínica ubicada en el centro de la esfera.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Teniendo en cuenta que la iluminancia depende del área que se pretende iluminar, debe ser medida en un sitio con un instrumento denominado luxómetro, el cual tiene tres características importantes: sensibilidad o rango de medida, corrección de color para ajustarlo a la sensibilidad del observador fotópico normalizado y corrección coseno que permite al instrumento realizar mediciones de luz en direcciones diferentes a la normal.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("En cuanto al procedimiento de medida, los numerales 490 (iluminación interior) y 540 (iluminación exterior) del RETILAP establecen que el espacio debe ser dividido usualmente por una cuadrícula de 0.6 m de lado y el luxómetro se debe ubicar sucesivamente en el centro de cada cuadrado a la altura del plano de trabajo usado en el diseño. Si no se cuenta con este valor se consideran para iluminación interior dos planos de trabajo, uno a 0,75 m (actividades sentado) y otro a 0,85 m (actividades de pie), ambos medidos sobre el nivel del suelo. Para iluminación exterior el plano de trabajo se considera a 0.15 m sobre el nivel del suelo. ")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Se calcula la media aritmética de todas las lecturas tomadas y ese valor debe corresponder con el valor de iluminancia promedio del diseño para cada plano de trabajo. Los numerales del RETILAP mencionados suministran modelos de mallas de m